@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 5px;
}

:root {
  --byte-grey: #929292;
  --byte-orange: #f4a41b;
  --byte-gold: #efc326;
  --shadow-green: rgb(32, 37, 32);
}

.byte-button {
  min-width: 208px;
  height: 58px;
  background-color: white;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
  border-color: transparent;
  /* Label Settings */
  color: #ff5c00;
  font-size: 18px;
  font-family: "A Goblin Appears";
  text-decoration: none;
}

.byte-button:hover {
  cursor: pointer;
  background-color: #ff5c00;
  color: white;
}

.entire-page {
  display: grid;
  height: 100%;
  min-height: 90vh;
  padding: 0px 5vw 30px 5vw;
  grid-template-columns: 1fr;
  grid-template-rows: 150px auto;
  grid-template-areas:
    "title"
    "body";
}

.login-divider {
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-divider-line {
  width: 88px;
  height: 3px;
  background-color: white;
  margin: 5px 10px;
}

.page-content-container {
  grid-area: body;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.diet-button-section {
  display: grid;
  grid-template-columns: repeat(auto-fill, 180px);
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  width: 80%;
  max-width: 750px;
  margin: auto;
  border: 4px dashed var(--byte-orange);
  border-radius: 15px;
  padding: 30px;
}

.diet-side-menu-buttons {
  position: fixed;
  right: 20px;
  top: 100px;
}

.diet-row {
  display: flex;
  flex-direction: row;
  justify-content: center;

  margin: 10px;
}

.load-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  width: 20%;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 60%;
  box-sizing: border-box;
}

.login-container label {
  color: var(--byte-orange);
  font-size: 1.5rem;
}

.login-container form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 30px;
  box-sizing: border-box;
}

.login-input {
  width: 100%;
  margin: 10px;
  padding: 10px;
  font-size: 1.2rem;
  border-radius: 15px;
  box-sizing: border-box;
}

.contact-container {
  border-radius: 25px;
  color: white;
  height: 70vh;
  margin: 30px 40px 30px 40px;
  /* display: flex; */
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: "A Goblin Appears";
}

.contact-text {
  margin-top: 50px;
  line-height: 50px;
  text-align: left;
  margin-left: 200px;
}

.footer {
  grid-area: footer;
  height: max(10vh, 300px);
  color: var(--byte-grey);
  background-color: var(--shadow-green);
  padding: 125px 60px 20px 60px;
  box-sizing: border-box;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
}

.footer-sectionheader-container {
  width: 343px;
  border-bottom: 2px #888888 solid;
}

.footer-sectionheader-title {
  font-family: "A Goblin Appears";
  margin: 0px 5px 2px 10px;
}

.footer-section-links {
  display: flex;
  flex-flow: column wrap;
  max-height: 100px;
  max-width: 40%;
  margin-left: 10px;
  margin-top: 5px;
}

.footer-social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 100px;
  max-width: 120px;
  margin: auto;
}

.footer-social-links a {
  color: whitesmoke;
  text-decoration: none;
}

.footer-section-links p {
  margin: 2px 30px 2px 0px;
}

.profile-dropdown-container {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 20px;
  color: #4d4d4d;
  font-family: "Arial";
}

.profile-dropdown-inner-container {
  display: flex;
  padding: 10px;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  background-color: #ffcd78;
  border-radius: 10px;
  width: 95%;
  box-sizing: border-box;
  text-align: center;
}

.profile-button {
  position: relative;
  width: 50px;
  height: auto;
  border-radius: 5px;
  cursor: pointer;
}

.profile-dropdown {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 65px;
  right: 5px;
  min-width: 200px;
  max-width: 250px;
  z-index: 1;
  padding: 10px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background-color: whitesmoke;
  box-shadow: 0px 4px 4px #00000025;
}

.hidden {
  display: none;
}

.profile-dropdown #user {
  margin-bottom: 15px;
}

.small {
  font-size: 0.7rem;
}

.profile-inner-icon {
  width: 70px;
  height: auto;
}

.signout-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  margin-top: 3px;
  width: 100%;
  border-radius: 8px;
  background-color: #ff9a61;
  color: #4d4d4d;
  cursor: pointer;
}

.header {
  position: fixed;
  width: 100%;
  background-color: var(--shadow-green);
  display: flex;
  z-index: 1;
  height: 69px;
  padding: 0px 50px 0px 50px;
  box-sizing: border-box;
}
.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.logo-font {
  font-size: 1.6rem;
}
.navbar {
  display: flex;
  flex-basis: 100%;
  justify-content: flex-end;
  align-items: center;
}

.navlink {
  margin: 10px;
  text-decoration: none;
  font-weight: bolder;
  font-family: "Arial";
  font-size: 1rem;
  font-weight: normal;
  color: white;
}

.navlink-selected {
  color: var(--byte-orange);
}

.navlink:hover {
  color: var(--byte-orange);
}

.navlink-line {
  background-color: white;
  height: 2px;
  width: 0%;
  margin: auto;
  margin-top: 2px;
  transition: 0.25s ease-in-out;
}

.navlink:hover > .navlink-line {
  width: 80%;
}
.navlink-selected > .navlink-line {
  width: 80%;
}

.snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 1rem;
}

.snackbar.show {
  visibility: visible; /* Show the snackbar */
  /* Add animation: Take 0.5 seconds to fade in and out the snackbar. 
   However, delay the fade out process for 2.5 seconds */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the snackbar in and out */

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
