.App {
  text-align: center;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.goblin-text {
  font-family: "A Goblin Appears";
}

.body {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(180deg, #efc326 0%, #ff5c00 100%);
}

.body > .login-hero {
  color: white;
  display: flex;
  height: 100%;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.clicker {
  position: relative;
  background-color: yellowgreen;
  border: none;
  padding: 0 20px;
  border-radius: 20px;
  box-shadow: 0px 4px rgb(57, 77, 18);
  min-width: 120px;
  outline: none;
}
.clicker:hover {
  cursor: pointer;
  background-color: rgb(125, 165, 45);
}
.clicker:active {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
  box-shadow: 0px 2px rgb(57, 77, 18);
}

/* Default Styles */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", "sans-serif";
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: "A Goblin Appears";
  src: local("A Goblin Appears"), url("/static/media/A Goblin Appears.4c9c1f1e.otf");
}

